import React from "react";
import TodoForm from "./TodoForm";
import './App.css'
const Todo = (props) =>{
    return(
        <>
        <main>
            <div className="headerText">
                <h2>Daily Schedule</h2>
            </div>
            <div className="content">
                <p>Now you can schedule your daily task</p>
            </div>
            <TodoForm inp={props.inp} change={props.change} add={props.add} inparr={props.inparr} delete={props.delete} view={props.view}/>
        </main>
        </>
    );
}

export default Todo;