import React,{useRef} from "react";
import './App.css'
const TodoForm = (props) =>{
    const inpref = useRef(null);
    const fo = () =>{
        inpref.current.focus();
    }
    return(
        <>
        <form>
            <div className="inp">
            <label>Enter Your Daily Chores</label>
            <input type="text" 
            name="schedule" 
            ref={inpref} 
            onClick={fo}
             placeholder="ex,breakfast at 8 am"
             value={props.inp}
             onChange={(e) => props.change(e.target.value)}
             ></input>
            
            <button type="button" onClick={props.add}>Save</button>

            </div>  
        </form>
        <section className="dispalay">
        {
            props.inparr.map((elem,index)=>{
                return(
                    <>
                    <ul>
                        <li key={index} onClick={()=>props.view(elem)}>{elem}
                        <button type="button" onClick={()=>props.delete(elem)}>Delete</button></li>
                    </ul>
                    </>
                )
            })
        }
        </section>
        </>
    )
}
export default TodoForm;