import React,{useState} from 'react'
import Header from './Header';
import Footer from './Footer';
import Todo from './Todo';
const App = () =>{
  const[inp,setinp] = useState('');
  const[inparr,setinparr] = useState([]);

  const change = (e)=>{
   setinp(e);
  }

  const add = () =>{
    setinparr([inp,...inparr]);
    setinp('');
  }
const view=(e)=>{
  prompt(e);
return(
  <section className="view">
  <div>
  <h3>{e}</h3>
  </div>
</section>
)
}
  const dele = (id) =>{
  setinparr(inparr.filter((elem)=>elem!==id))
  }
  return(
    <>
    <Header/>
    <Todo inp={inp} change={change} add={add} inparr={inparr} delete={dele} view={view}/>
    <Footer/>
    </>
  )
}

export default App;